<template>
    <div>
        <!-- 排行榜顶部 -->
         <div class="blank"></div>
        <van-nav-bar>
            <div slot="left">
                <span @click="prev"></span>
            </div>
            <div slot="title">
                <h1>排行榜</h1>
            </div>
        <van-icon  slot="right" >
                <i @click="show"></i>
                <b @click="search"></b>
        </van-icon>
        </van-nav-bar>
         <transition name="top" >
        <div class="application" v-show="isshow">
            <ul>
                <li>
                    <div class="sort" @click="sort">
                        <span ></span>
                        <p>分类</p>
                    </div>
                </li>
                <li>
                     <div class="index" @click="index">
                        <span ></span>
                        <p>首页</p>
                     </div>
                </li>
            </ul>
            <span class="button" @click="my"></span>
        </div>
        </transition>
        <div class="mask" v-show="isshow" @click="show">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            isshow:false,
        }
    },
    methods:{
        show(){
            if(this.isshow){
                this.isshow=false;
            }else{
                 this.isshow=true;
            }
        },
         search(){
            this.$router.push({path:"/search"});
        },
        sort(){
            this.$router.push({path:"/sort"});
        },
        index(){
           this.$router.push({path:"/index"});
        },
         my(){
          if(this.$store.state.user.islogin==true){
                this.$router.push({path:"/my"});
            }else{
                 this.$router.push({path:"/login"});
            }
        },
        prev(){
            this.$router.go(-1);
        }
    }
}
</script>
<style lang="scss" scoped>
.blank{
