<template>
   <div class="rank">
       <!-- 排行榜前十名漫画 -->
        <div class="top">
        <div class="left"  @click="$store.commit('books/details',{item})" v-for="(item,index) in $store.state.books.top_two" :key="'left'+index">
            <img :src="item.src" alt="">
            <strong>{{item.name}}</strong>
            <small>{{item.num}}</small>
        </div>
        <div class="center"  @click="$store.commit('books/details',{item})" v-for="(item,index) in $store.state.books.top_one" :key="'center'+index">
            <img :src="item.src" alt="">
            <strong>{{item.name}}</strong>
            <small>{{item.num}}</small>
        </div>
         <div class="right"  @click="$store.commit('books/details',{item})"  v-for="(item,index) in $store.state.books.top_three" :key="'right'+index">
            <img :src="item.src" alt="">
            <strong>{{item.name}}</strong>
            <small>{{item.num}}</small>
        </div>
    </div>
    <div class="content">
        <div class="rank"  @click="$store.commit('books/details',{item})" v-for="(item,index) in $store.state.books.top_ten" :key="'rank'+index">
            <div class="pic">
            <img v-bind:src="item.src" alt="">
            </div>
            <div class="word">
            <strong>{{item.name}}</strong>
            <small>{{item.num}}</small>
            <small>{{item.sort}}</small>
            <small>{{item.details}}</small>
            <i>{{item.rank}}</i>
        </div>
        </div>
    </div>
   </div>
</template>
<script>
export default {
    watch:{
      "$store.state.books.isdetails":function(newvalue){
            if(newvalue==true){
                this.$router.push({path:"/details"});
            }
        }
    },
    beforeDestroy(){
       this.$store.commit('books/jump_details');
    }
}
</script>
<style lang="scss" scoped>
    @mixin top3($width,$height,$color){
            width:$width;
            background-color:$color;
            background-position:right bottom;
            background-repeat:no-repeat;
            background-size:auto 2.25rem;
            img{
                display:block;
                width:$width;
                height:$height;
            }
            strong{
                display:block;
                font-size:0.8rem;
                padding-bottom:0.15rem;
                color:white;
                text-align:center;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
            }
            small{
                display:block;
                font-size:0.6rem;
                padding-bottom:0.35rem;
                color:white;
                text-align:center;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;

            }
        }
        @mixin rank{
            
            background-color:white;
            margin-bottom:0.25rem;
            padding:0.25rem;
            display:flex;
            justify-content: center;
            position: relative;
            .pic{
            img{
                display:block;
                width:3.5rem;
                height:4.675rem;
                margin-right:0.5rem;
            }
            }
            .word{
                overflow:hidden;
            strong{
                display:block;
                color:#535252;
                font-size:0.8rem;
                padding-top:0.25rem;
                padding-right:2rem;
                line-height: 150%;

            }
            small{
                display:block;
                font-size:0.6rem;
                color:#969696;
                padding-top: 1px;
                white-space:nowrap;
                overflow:hidden;
                text-overflow:ellipsis;
                line-height: 1rem;
            }
            i{
                position: absolute;
                font-size: 1.5rem;
                color: #C5C5C5;
                right: 0.45rem;
                line-height: 100%;
                top: 0.45rem;
                display: block;
                font-style: normal;
            }
            }
        }
        
    .top{
        padding-top:0.95rem;
        padding-bottom:1.1rem;
        background-color:#f5f5ee;
        width:100%;
        background-image:url(../../assets/rank-bg.png);
        background-position: center bottom;
        background-repeat: no-repeat;
        background-size: 100% auto;
        display: flex;
        align-items: flex-end;
        justify-content: center;
        .left{
            background-image:url(../../assets/top3_2bg.jpg);
            @include top3(4.35rem,5.8rem,#ffa755);
        }
        .center{
            background-image:url(../../assets/top3_1bg.jpg);
            margin:0 1rem;
            @include top3(5.25rem,7rem,#e86a3e);
        }
        .right{
            background-image:url(../../assets/top3_3bg.jpg);
            @include top3(4.35rem,5.8rem,#ffa755);
        }
       
    }
    .content{
        padding:1rem 0.25rem 0.25rem 0.25rem;
        background-color:#f5f5ee;
        .rank{
            @include rank;
        }
       
    }
</style>